import React from 'react';
import './Sequencer.css';

export const BlocksStructure = ({ 
  sequencerStructure, 
  trackNames, 
  getCellState, 
  handleCellClick,
  activeNotes = [], // Активные ноты для подсветки
  currentBlock = 0 // Текущий активный блок  
}) => (
  <div className="blocks-container">
    {sequencerStructure.map((block, blockIndex) => (
      <div 
        key={block.id || `block-${blockIndex}`} 
        className={`block-container ${blockIndex === currentBlock ? 'active' : 'inactive'}`}
      >
        <div className="sequencer">
          <div className="sequencer-header">
            <h2>{block.name}</h2>
            <button className="loop-button">LOOP</button>
          </div>

          <div className="grid">
            {trackNames.map(trackName => (
              <div key={trackName} className="track-row">
                {Array.from({ length: block.trackSteps[trackName] }, (_, stepIndex) => {
                  const cellId = `${blockIndex}-${trackName}-${stepIndex}`;
                  const cellState = getCellState(cellId);
                  const isActive = !!cellState.instrumentId;
                  const isPlaying = activeNotes.includes(cellId);

                  return (
                    <div
                      key={cellId}
                      className={`grid-cell ${isActive ? 'active' : ''} ${isPlaying ? 'playing' : ''}`}
                      onClick={() => handleCellClick(cellId)}
                      data-cell-id={cellId}
                    >
                      {cellState.instrumentId && (
                        <span className="instrument-indicator">
                          {cellState.instrumentId.substring(0, 3)}
                        </span>
                      )}
                    </div>
                  );
                })}
              </div>
            ))}
          </div>
        </div>
      </div>
    ))}
  </div>
);